/*MIS ESTILOS*/
a.navbar-brand img {
  width: 191px;
  padding: 0px;
}

.menu_btns {
    padding: 20px 0px 20px 30px;
    background: #003253;
}

.navbar {
padding: 0px;
/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+45,003153+45,ffffff+45,003153+47,003153+100 */
background: #ffffff; /* Old browsers */
background: -moz-linear-gradient(left, #ffffff 0%, #ffffff 45%, #003153 45%, #ffffff 45%, #003153 47%, #003153 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(left, #ffffff 0%,#ffffff 45%,#003153 45%,#ffffff 45%,#003153 47%,#003153 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to right, #ffffff 0%,#ffffff 45%,#003153 45%,#ffffff 45%,#003153 47%,#003153 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#003153',GradientType=1 ); /* IE6-9 */
}

.btns li a {
    color: white !important;
    font-family: 'Oswald', sans-serif;
    font-weight: 100;
    font-size: 18px;
    text-transform: uppercase;
}

.dropdown-toggle::after {
    display: inline-block;
    width: 0;
    height: 0;
    color: #01abd4;
    margin-left: 0.255em;
    vertical-align: 0.255em;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-bottom: 0;
    border-left: 0.3em solid transparent;
}
.busqueda_flex {
    display: flex;
}
.btns span {
    padding-left: 15px;
    padding-right: 15px;
    color: #01abd4;
}

.btns {
    display: flex;
    align-items: center;
}



/*--------- LAPTOP ---------*/
@media screen and (max-width: 991px) {

  .menu_btns {
    padding: 0px;
    background:inherit;
}
  .navbar {
    padding: 0px !important; 
    background: #ffffff !important;
}
}

/*--------- MOVIL ---------*/
@media screen and (max-width: 768px) {
  .menu {
    display: flex !important;
    flex-direction: row !important;
    flex-wrap: wrap;
  }
  a.navbar-brand img {
    width: 120px;
    padding: 0px;
  }
}

